.emptyCart{
    margin: auto;
    text-align: center;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 70vh;
    padding: 10vmax;
}
.emptyCart > svg{
    font-size: 5vmax;
    color: tomato;
}
.emptyCart > p{
    font-size: 2vmax;
    margin-bottom: 2vmax;
}
.emptyCart > a{
    background-color: rgb(51,51,51);
    color: white;
    border: none;
    padding: 1vmax 3vmax;
    cursor: pointer;
    font: 400 1vmax "Roboto";
    text-decoration: none;
}
.cartPage{
    padding: 3vmax;
    min-height: 70vh;
}
.cartHeader{
    background-color: tomato;
    width: 90%;
    box-sizing: border-box;
    margin: auto;
    color: white;
    display: grid;
    grid-template-columns: 4fr 1fr 1fr;
    font: 300 1vmax "Roboto";
}
.cartHeader > p{
    margin: 10px;
}
.cartHeader > p:last-child{
    text-align: end;
}

.cartContainer{
    width: 90%;
    padding-bottom: 2vmax;
    margin: auto;
    display: grid;
    grid-template-columns: 4fr 1fr 1fr;
}
.cartInput{
    display: flex;
    align-items: center;
    height: 8vmax;
}
.cartInput>button{
    border: none;
    background-color: rgba(0,0,0,0.616);
    cursor: pointer;
    color: white;
    transition: all 0.5s;
}
.cartInput > button:hover{
    background-color: rgba(0,0,0,0.767);
}
.cartInput > input{
    border: none;
    padding: 0.5vmax;
    width: 2vmax;
    font: 400 0.9vmax "Roboto";
    color: rgba(0,0,0,0.74);
    outline: none;
    text-align: center;
}
.cartSubtotal{
    display: flex;
    padding: 0.5vmax;
    height: 8vmax;
    box-sizing: border-box;
    font: 300 1vmax cursive;
    justify-content: flex-end;
    align-items: center;
    color: rgba(0,0,0,0.753);
}
.cartGrossTotal{
    display: grid;
    grid-template-columns: 2fr 1.2fr
}
.cartGrossTotalBox{
    border-top: 3px solid tomato;
    margin: 1vmax 4vmax;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    font: 300 1vmax "Roboto";
    padding: 2vmax 0;
}
.checkBtn{
    display: flex;
    justify-content: flex-end;
}
.checkBtn > button{
    background-color: tomato;
    color: white;
    border: none;
    padding: 0.8vmax 4vmax;
    width: 50%;
    font: 300 0.8vmax "Roboto";
    margin: 1vmax 4vmax;
    cursor: pointer;
    border-radius: 30px;
}
@media screen and (max-width:600px) {
    .emptyCart{
        height: 70vh;
    }
    .cartPage{
        padding: 0;
        min-height: 70vh;
    }
    .cartHeader{
        width: 100%;
        font: 300 1.8vmax "Roboto";
        grid-template-columns: 3fr 1fr 1fr;
    }
    .cartContainer{
        padding: 0;
        width: 100%;
        grid-template-columns: 3fr 1fr 1fr;
    }
    .cartInput{
        height: 20vmax;
    }
    .cartInput > button{
        padding: 1vmax;
    }
    .cartInput > input{
        width: 2.2vmax;
        font: 400 1.9vmax "Roboto";
    }
    .cartSubtotal{
        padding: 1.5vmax;
        font: 300 2vmax "Roboto";
        height: 20vmax;
    }
    .cartGrossTotal{
        display: grid;
        grid-template-columns: 0fr 2fr;
    }
    .cartGrossTotalBox{
        padding: 2vmax;
        font: 300 2vmax "Roboto";
    }
    .checkBtn > button{
        padding: 2vmax 4vmax;
        width: 100%;
        font: 300 2vmax "Roboto";
    }

}