.productReviewsContaier{
    width: 100%;
    box-sizing: border-box;
    background-color: rgb(255,255,255);
    border-left: 1px solid rgba(0,0,0,0.158);
    height: auto;
}
.productReviewsForm{
    width: 20rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 3vmax;

}
.productReviewsFormHeading{
    color: rgba(0,0,0,0.733);
    font: 300 2rem "Roboto";
    text-align: center;
}
.productReviewsForm > div{
    display: flex;
    width: 100%;
    align-items: center;
    margin: 2rem;
}
.productReviewsForm > div >input{
    padding: 1vmax 4vmax;
    padding-right:1vmax ;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0,0,0,0.267);
    border-radius: 4px;
    font:300 0.9rem cursive;
    outline: none;
}
.productReviewsForm > div >svg{
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0,0,0,0.623);
}
@media screen and (max-width:600px) {
    .productReviewsContaier{
        border-left: none;
        border-top: 1px solid rgba(0,0,0,0.158);
    }
    .productReviewsForm>div>input{
        padding: 2.5vmax 5vmax;
        font:300 1.7vmax cursive;
    }
    .productReviewsForm >div>svg{
        font-size: 2.8vmax;
    }
}