.shippingContainer{
    width: 100vw;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.shippingBox{
    background-color: white;
    width: 25vw;
    height: 90vh;
    box-sizing: border-box;
    overflow: hidden;
}
.shippingHeading{
    text-align: center;
    color: rgba(0,0,0,0.664);
    font: 400 1.4vmax "Roboto";
    padding: 1.3vmax;
    border-bottom: 1px solid rgba(0,0,0,0.205);
    width: 60%;
    margin: auto;
}
.shippingForm{
    height: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    justify-content: space-evenly;
    transition: all 0.5s;
}
.shippingForm >div{
    display: flex;
    width: 100%;
    align-items: center;
}
.shippingForm >div >input{
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0,0,0,0.267);
    border-radius: 4px;
    font: 300 0.9vmax cursive;
    outline: none;
}
.shippingForm>div>select {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 0.9vmax cursive;
    outline: none;
}
.shippingForm>div>svg{
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0,0,0,0.623);
}
.shippingBtn{
    border: none;
    background-color: tomato;
    margin: 2vmax;
    color: white;
    font: 300 1vmax "Roboto";
    width: 100%;
    padding: 1vmax;
    cursor: pointer;
    transition: all 0.5s;
    outline: none;
}
.shippingBtn:hover{
    color: tomato;
    background-color: white;
    border: 1px solid tomato;
    border-radius: 1vmax;
}
@media screen and (max-width:600px) {
    .shippingBox{
        width: 100vw;
        height: 95vh;
    }
    .shippingHeading{
        font: 400 6vw "Roboto";
        padding: 5vw;
        width: 100%;
    }
    .shippingForm > div >input{
        padding: 3vw 10vw;
        font: 300 4vw cursive;
    }
    .shippingForm>div>select {
        padding: 3vw 10vw;
        font: 300 4vw cursive;
    }
    .shippingBtn{
        font: 300 4vw "Roboto";
        padding: 4vw;
    }
}