.ProductDetails{
    /* background-color: rgb(238, 231, 231); */
    width: 100vw;
    max-width: 100%;
    box-sizing: border-box;
    padding: 6vmax;
    display: flex;
}

.cr{
    width: 100%;
    height: 85vh;
}
.CarouselImage{
    width: 30vmax;
}

.ProductDetails > div{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: 2vmax;
    box-sizing: border-box;
    border: 1px solid white;
}

.ProductDetails > div:last-child{
    align-items: flex-start;
}

.detailsBlock-1 > h2{
    color: rgb(54, 54, 54);
    font: 600 1.6vmax "Roboto";
}
.detailsBlock-1 > p{
    color: rgba(54,54,54,0.582);
    font:200 0.6vmax cursive;
}
.detailsBlock-2{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-top: 1px solid rgba(0,0,0,0.205);
    border-bottom: 1px solid rgba(0,0,0,0.205);
    width: 70%;
    padding: 0.7vmax 0;
}
.detailsBlock-2-span{
    font:200 0.9vmax cursive;
    color: rgba(0, 0, 0, 0.473);
}
.detailsBlock-3{
    width: 70%;
}
.detailsBlock-3 > h1{
    color:rgba(17, 17, 17, 0.795);
    font:400 1.4vmax "Franklin Gothic Medium";
}
.detailsBlock-3-1{
    display: flex;
    align-items: center;
}
.detailsBlock-3-1-1 >button{
    border: none;
    background-color: rgba(48, 46, 46, 0.616);
    padding: 0.5vmax;
    cursor: pointer;
    color: white;
    transition: all 0.5s;
}
.detailsBlock-3-1-1 > button:hover{
    background-color: rgba(0,0,0,0.767);

}

.detailsBlock-3-1-1 > input{
    border: none;
    padding: 0.5vmax;
    width: 2vmax;
    text-align: center;
    outline: none;
    font:400 0.8vmax "Roboto";
    color: black;
}

.detailsBlock-3-1 > button:last-child{
    border:1px solid rgb(236, 41, 7);
    cursor: pointer;
    color: white;
    transition: all 0.5s;
    background-color: tomato;
    font: 500 0.9vmax "Roboto";
    border-radius: 10px;
    padding: 0.6vmax 1.8vmax;
    margin: 1.2vmax;
    outline: none;
}

.detailsBlock-3-1 > button:last-child:hover{
    background-color: white;
    border: 1px solid rgb(236, 41, 7);
    color: rgb(241, 53, 20);
}

.detailsBlock-3 > p{
    border-top: 1px solid rgba(0, 0,0,0.205);
    border-bottom: 1px solid rgba(0,0,0,0.205);
    padding: 1vmax 0;
    color: rgba(0, 0,0, 0.651);
}

.detailsBlock-4{
    color: rgba(0,0,0,0.897);
    font: 500 1.3vmax "sans-serif";
}

.detailsBlock-4 > p{
    color:rgba(0, 0,0, 0.534);
    font:300 0.9vmax "sans-serif";
}

.submitButton{
    border: none;
    background-color: tomato;
    font: 500 1vmax "Roboto";
    border-radius: 10px;
    padding: 0.6vmax 1.8vmax;
    margin: 1vmax 0;
    color: white;
    cursor: pointer;
    transition: all 0.4s;
    outline: none;
}

.submitButton:hover{
    background-color: rgb(194, 50, 25);
    transform: scale(1.1);
}
.submitDialog{
    display: flex;
    flex-direction: column;
}
.reviews{
    display: flex;
    overflow: auto;
}

.reviewsHeading{
    color: rgb(61, 58, 58);
    font: 500 1.4vmax;
    font-family: "Verdana";
    text-align: center;
    border-bottom: 1px solid rgba(0,0,0,0.226);
    padding: 1vmax;
    width: 20vmax;
    margin: auto;
    margin-bottom: 4vmax;
}

.reviewCard{
    box-shadow: 0 0 5px rgba(0,0,0,0.226);
    border: 1px solid rgba(56,56,56,0.116);
    width: 30vmax;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1vmax;
    padding: 3vmax;
}

.reviewCard > img{
    width: 6vmax;
}
.reviewCard > p{
    color: rgba(0,0,0,0.836);
    font: 600 0.9vmax;
}
.reviewCardComment{
    color: rgba(0, 0,0, 0.444);
    font: 300 0.8vmax cursive;
}
.noReviews{
    font: 400 1.3vmax "Gill Sans";
    text-align: center;
    color: rgba(0,0,0,0.548);
}

@media screen and (max-width:600px){
    .ProductDetails{
        flex-direction: column;
        height: unset;
    }
    .cr{
        height: 60vh;
    }
    .ProductDetails > div:last-child{
        align-items: center;
    }
    .detailsBlock-1 > h2{
        font-size: 2.8vmax;
        text-align: center;
    }

    .detailsBlock-1 > p{
        text-align: center;
        font-size: 1vmax;
    }
    .detailsBlock-2{
        justify-content: center;
    }
    .detailsBlock-3 > h1{
        font: 700 3vmax "Franklin Gothic Medium";
        text-align: center;
    }
    .detailsBlock-3-1{
        flex-direction: column;
    }
    .detailsBlock-3-1-1{
        padding: 2vmax 0;
    }
    .detailsBlock-3-1-1 > button{
        padding: 1.2vmax;
        width: 4vmax;
    }
    .detailsBlock-3-1-1 > input{
        padding: 1.5vmax;
        width: 4vmax;
        font:400 2vmax "Roboto";
    }
    .detailsBlock-3-1 > button:last-child{
        font: 500 1.7vmax "Roboto";
        padding:1.5vmax;
        width: 20vmax;
        margin:3vmax 0;
    }
    .detailsBlock-3 > p{
        padding: 2.5vmax 0;
        text-align: center;
        font:400 2vmax "Roboto"
    }
    .detailsBlock-4{
        font: 500 2.5vmax "sans-serif";
    }
    .detailsBlock-4 > p{
        font: 300 1.8vmax "sans-serif";
    }
    .submitButton{
        font:500 1.7vmax "Roboto";
        padding:1.5vmax;
        width: 20vmax;
        margin: 3vmax 0;
    }
    .reviewCard > p{
        font:600 3vw "Roboto";
    }
    .reviewCardComment{
        font: 300 5vw cursive;
    }
}